$small-device: 'only screen and (max-width : 320px)';

@mixin user-select() {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@mixin mat-icon-size($size: 1em) {
  font-size: $size;
  width: $size;
}

@mixin background-size($param) {
  -webkit-background-size: $param;
  -moz-background-size: $param;
  -o-background-size: $param;
  background-size: $param;
}

@mixin border-radius($radius) {
  border-radius: $radius;
  -webkit-border-radius: $radius;
  -moz-border-radius: $radius;
}

@mixin border-radii($topleft, $topright, $bottomright, $bottomleft) {
  border-top-left-radius: $topleft;
  border-top-right-radius: $topright;
  border-bottom-right-radius: $bottomright;
  border-bottom-left-radius: $bottomleft;
  -webkit-border-top-left-radius: $topleft;
  -webkit-border-top-right-radius: $topright;
  -webkit-border-bottom-right-radius: $bottomright;
  -webkit-border-bottom-left-radius: $bottomleft;
  -moz-border-radius-topleft: $topleft;
  -moz-border-radius-topright: $topright;
  -moz-border-radius-bottomright: $bottomright;
  -moz-border-radius-bottomleft: $bottomleft;
}

@mixin box-shadow($left, $top, $radius, $color) {
  box-shadow: $left $top $radius $color;
  -webkit-box-shadow: $left $top $radius $color;
  -moz-box-shadow: $left $top $radius $color;
}

@mixin gradient($color1, $color2) {
  background-color: $color1;
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, startColorstr=#{$color1}, endColorstr=#{$color2});
  background-image: -moz-linear-gradient(center top, $color1, $color2);
  background-image: -webkit-gradient(linear,
      0% 0%,
      0% 100%,
      from($color1),
      to($color2));
}

@mixin transition($property, $duration, $easing: linear) {
  transition: $property $duration $easing;
  -webkit-transition: $property $duration $easing;
  -moz-transition: $property $duration $easing;
  -o-transition: $property $duration $easing;
}

@mixin height-calc($height, $minus) {
  height: calc(#{$height} - #{$minus});
  height: -o-calc(#{$height} - #{$minus});
  height: -webkit-calc(#{$height} - #{$minus});
  height: -moz-calc(#{$height} - #{$minus});
}

@mixin width-calc($width, $minus) {
  width: calc(#{$width} - #{$minus});
  width: -o-calc(#{$width} - #{$minus});
  width: -webkit-calc(#{$width} - #{$minus});
  width: -moz-calc(#{$width} - #{$minus});
}

@mixin breakpoint($point) {
  @if $point==tablet {
    @media (min-width: 768px) and (max-width: 1024px) {
      @content;
    }
  }

  @else if $point==phone {
    @media (max-width: 767px) {
      @content;
    }
  }

  @else if $point==desktop {
    @media (min-width: 1025px) {
      @content;
    }
  }
}