@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url('~src/assets/fonts/material-icons/Material-Icons.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('~src/assets/fonts/material-icons/Material-Icons.ttf') format('truetype'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

@font-face {
  font-family: 'Material Icons Outlined';
  font-style: normal;
  font-weight: 400;
  src: url('~src/assets/fonts/material-icons/Material-Icons-Outlined.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('~src/assets/fonts/material-icons/Material-Icons-Outlined.otf') format("opentype"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

@font-face {
  font-family: 'Material Icons Two Tone';
  font-style: normal;
  font-weight: 400;
  src: url('~src/assets/fonts/material-icons/Material-Icons-Two-Tone.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('~src/assets/fonts/material-icons/Material-Icons-Two-Tone.otf') format("opentype"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

@font-face {
  font-family: 'Material Icons Round';
  font-style: normal;
  font-weight: 400;
  src: url('~src/assets/fonts/material-icons/Material-Icons-Round.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('~src/assets/fonts/material-icons/Material-Icons-Round.otf') format("opentype"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

@font-face {
  font-family: 'Material Icons Sharp';
  font-style: normal;
  font-weight: 400;
  src: url('~src/assets/fonts/material-icons/Material-Icons-Sharp.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('~src/assets/fonts/material-icons/Material-Icons-Sharp.otf') format("opentype"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

@mixin material-icons {
  font-weight: normal;
  font-style: normal;
  letter-spacing: normal;
  text-transform: none;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}

.material-icons {
  font-family: 'Material Icons';
  @include material-icons;
}

.material-icons-outlined {
  font-family: 'Material Icons Outlined';
  @include material-icons;
}

.material-icons-two-tone {
  font-family: 'Material Icons Two Tone';
  @include material-icons;
}

.material-icons-round {
  font-family: 'Material Icons Round';
  @include material-icons;
}

.material-icons-sharp {
  font-family: 'Material Icons Sharp';
  @include material-icons;
}
