.modal-dialog {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow-y: auto;
  min-height: calc(100vh - 60px);

  @media (max-width: 767px) {
    min-height: calc(100vh - 20px);
  }
}

.modal-content {
  border-radius: 0px;

  h1 {
    font-family: 'Roboto';
    font-weight: 700;
    font-size: 24px;
    color: #58595B;
  }

  p {
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 12px;
    color: #58595B;
  }

  .modal-header {
    border-bottom: 0px;
  }

  img {
    margin: 10px 0px;
  }
}

.btn-confirm-modal {
  margin-bottom: 20px;
  padding: 9px 68px;
  background-color: #F47920;
  color: #fff;
  font-size: 18px;
  font-family: 'Roboto';

  @media (max-width: 321px) {
    padding: 9px 38px;
  }

  @media (min-width: 321px) and (max-width: 376px) {
    padding: 9px 48px;
  }

  @media (min-width: 376px) and (max-width: 426px) {
    padding: 9px 62px;
  }
}

.btn-cancel-modal {
  border: 1px solid #F47920;
  color: #F47920;
  background-color: #fff;
  font-family: 'Roboto';
  font-size: 18px;
  padding: 8px 56px;
  margin-right: 10px;

  @media (max-width: 321px) {
    padding: 8px 26px;
  }

  @media (min-width: 321px) and (max-width: 376px) {
    padding: 8px 36px;
  }

  @media (min-width: 376px) and (max-width: 426px) {
    padding: 8px 50px;
  }

  @media (max-width: 767px) {
    .modal-dialog {
      min-height: calc(100vh - 20px);
    }
  }
}

.modal-title {
  margin-bottom: 8px;
  line-height: 1.5;
}

.doc-upload-section {
  padding: 24px 44px 24px 44px;
  border: 1px solid #DCDCDC;
  margin: 24px 15px 24px 15px;
}