  }

  .circle-mark:after {
    content: "";
    position: absolute;
    display: none;
  }

  & input:checked ~ .circle-mark:after {
    display: block;
  }

  .circle:after {
    top: 9px;
    left: 9px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
