      right: 0;
    }

    .swiper-button-prev,
    .swiper-button-next {
      top: 58%;
      width: 20px;
      height: 22px;
      border-radius: 5px;
      background-color: #f97d00;
      background-image: none;

      &:after {
        color: #fff;
        font-size: 14px;
      }
    }
  }

  .movida-pagina-meus-cartoes {
    .swiper-button-prev {
      background-image: url($icon-prev);
      left: 0px;
    }

    .swiper-button-next {
      background-image: url($icon-next);
      right: 0px;
    }

    .swiper-button-prev,
    .swiper-button-next {
      top: 60%;
    }
  }
}

.swiper-hide-nav {
  .swiper-button-prev,
  .swiper-button-next {
    display: none;
  }
}

