$primary-lighter: #962fbb;
$primary-light: #722e8b;
$primary: #F47920;
$primary-dark: #531C67;
$primary-darker: #441754;
$primary-transparent: rgba(98, 34, 121, 0.5);
$secondary: #F47920;
$secondary-light: #f9bc8f;

$white: #fff;
