$gray-90: #707070;
$gray-95: #696969;
$gray-100: #606060;

.primary {
    color: $primary !important;
}

.secondary {
    color: rgba(255, 255, 255, 0.87);
}

a.secondary {
    color: $secondary !important;

    &:hover {
        color: $primary !important;
    }
}

.hover-secondary {
    &:hover {
        color: $secondary !important;

        path[fill="#ccc"] {
            fill: $secondary !important;
        }

        path[fill="#c4c4c4"] {
            fill: $secondary !important;
        }

        path[fill="#e3e3e3"] {
            fill: $secondary-light !important;
        }

        circle {
            fill: $secondary !important;
        }
    }
}

